:root {
  --font-body: Arial, sans-serif;
  --red-hat: "Red Hat Display", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --verypaleblue: hsl(225, 100%, 98%);
  --desaturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(223, 47%, 23%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background: var(--paleblue);
  font-family: var(--red-hat);
  font-size: 16px;
  margin: 0;
  padding: 0;
}
@media (min-width: 90em) {
  body::before {
    content: "";
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background-image: url("/images/pattern-background-desktop.svg");
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
  text-align: center;
  margin-top: 6.25rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.card {
  background: var(--white);
  display: inherit;
  margin: 0 auto;
  max-width: 25rem;
  border-radius: 1.25rem;
  overflow: hidden;
}
.card__hero {
  width: 100%;
}
.card__tittle {
  font-size: 1.5rem;
  font-weight: 900;
  text-align: center;
  color: var(--darkblue);
  margin-top: 1.875rem;
  margin-bottom: 1.25rem;
}
.card__description {
  color: var(--desaturatedblue);
  width: 85%;
  margin: 0 auto;
  margin-bottom: 1.25rem;
  line-height: 1.6;
  /* padding: 0 rem(70);
  margin-bottom: rem(20);

  @include breakpoint-down(small) {
      padding: 0 rem(30);
  } */
}
.card__plan {
  background: var(--verypaleblue);
  border-radius: 0.625rem;
  padding: 0.625rem 0;
  width: 80%;
  margin: 0 auto;
}